
html,
body,
#root{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}
@media screen and (max-width: 468px) {
      body{
        overflow-x: hidden;
      }
}


code {
  font-family: 'Roboto', sans-serif;
}
